@import "./styles/reset";
.container {
  max-width: 1200px;
  margin: 0 auto;
}
.App {
  background: grey;
  min-height: 100vh;
  background: linear-gradient(180deg, #e6d4de 0%, #9890c7 100%);
}
.start-loader {
  background: linear-gradient(180deg, #e6d4de 0%, #9890c7 100%);
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
